<template>
  <v-container grid-list-xs>
    <v-overlay :value="overlay">
      <v-progress-circular
          color="primary"
          indeterminate
          size="50"
      ></v-progress-circular>
      <v-btn icon @click="overlay = false">
        <v-icon>mdi-close</v-icon>
      </v-btn>
    </v-overlay>
    <v-navigation-drawer
      app
      class="primary darken-1 white--text"
      clipped
      permanent
      right
      style="z-index: 3;"
    >
      <v-list class="white--text">
        <v-list-item>
          <v-btn class="secondary" dark @click="$router.push('/resource/view/student/' + queries.practitioner)">
            <v-icon light left>mdi-eye</v-icon>
            <span>View Student</span>
          </v-btn>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
    <v-dialog
      v-model="activateDialog"
      persistent
      max-width="500px"
      transition="dialog-transition"
    >
      <v-card>
        <v-toolbar color="primary" class="darken-1 white--text text-uppercase font-weight-bold" dark>
          Confirm Activation
          <v-spacer></v-spacer>
          <v-btn icon color="white" @click="activateDialog = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-card-text>
          Are you sure you want to activate  <b>{{ practitionername }} </b><br>
          as a family member of <b>{{ currentHead.name }}</b>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="success" @click="activate">Yes, Proceed</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="deactivateDialog"
      persistent
      max-width="500px"
      transition="dialog-transition"
    >
      <v-card>
        <v-toolbar color="primary" class="darken-1 white--text text-uppercase font-weight-bold" dark>
          Confirm Deactivation
          <v-spacer></v-spacer>
          <v-btn icon color="white" @click="activateDialog = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-card-text>
          Are you sure you want to Deactivate  <b>{{ practitionername }} </b><br>
          as a family member of <b>{{ currentHead.name }}</b>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="error" @click="deactivate">Yes, Proceed</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="removeDialog"
      persistent
      max-width="500px"
      transition="dialog-transition"
    >
      <v-card>
        <v-toolbar color="primary" class="darken-1 white--text text-uppercase font-weight-bold" dark>
          Confirm Remove
          <v-spacer></v-spacer>
          <v-btn icon color="white" @click="removeDialog = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-card-text>
          Are you sure you want to remove  <b>{{ practitionername }} </b><br>
          as a family member of <b>{{ currentHead.name }}</b>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="error" @click="remove">Yes, Proceed</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <b>Student: {{ practitionername }} - {{ practitionerregnum }}</b>
    <v-card>
      <v-card-title>
        Current Family Head Details
      </v-card-title>
      <v-card-text>
        <v-row>
          <v-col>
            Name: <b>{{ currentHead.name }}</b>
          </v-col>
          <v-col>
            Registration Number: <b>{{ currentHead.regNum }}</b>
          </v-col>
          <v-col>
            Active: <b>{{ currentHead.status }}</b>
          </v-col>
        </v-row>
        <v-row>
          <v-col v-if="currentHead.statusCode === 'no'">
            <v-btn color="success" rounded @click="activateDialog = true">Activate To Family</v-btn>
          </v-col>
          <v-col v-if="currentHead.statusCode === 'yes'">
            <v-btn color="error" rounded @click="deactivateDialog = true">Deactivate From Family</v-btn>
          </v-col>
          <v-col>
            <v-btn color="error" rounded @click="removeDialog = true">
              <v-icon left>mdi-cancel</v-icon>
              Remove From Family
            </v-btn>
          </v-col>
        </v-row>
      </v-card-text>
    </v-card>
  </v-container>
</template>
<script>
export default {
  props: ["queries"],
  data() {
    return {
      date: '',
      menu: false,
      overlay: false,
      activateDialog: false,
      deactivateDialog: false,
      removeDialog: false,
      practitionername: '',
      practitionerregnum: '',
      familyHead: {},
      total: 0,
      name: '',
      regNum: '',
      loading: false,
      currentHead: {
        name: '',
        regNum: '',
        status: '',
        statusCode: ''
      }
    }
  },
  methods: {
    remove() {
      this.overlay = true
      this.deactivateDialog = false
      fetch("/site/student/removeFamilyHead", {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify({
          practitioner: this.queries.practitioner,
          familyHead: this.currentHead.id
        })
      }).then((response) => {
        if(response.status === 200) {
          this.$router.push("/resource/view/student/" + this.queries.practitioner)
          this.$store.commit("setMessage", {
            type: "success",
            text: "Family head deactivated"
          });
        } else {
          this.$store.commit("setMessage", {
            type: "error",
            text: "Failed to deactivate family head."
          });
        }
      }).catch(() => {
        this.overlay = false
        this.$store.commit("setMessage", {
          type: "error",
          text: "Failed to deactivate family head."
        });
      })
    },
    deactivate() {
      this.overlay = true
      this.deactivateDialog = false
      fetch("/site/student/deactivateFamilyHead", {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify({
          practitioner: this.queries.practitioner,
          familyHead: this.currentHead.id
        })
      }).then((response) => {
        if(response.status === 200) {
          this.getFamilyHead()
          this.$store.commit("setMessage", {
            type: "success",
            text: "Family head deactivated"
          });
        } else {
          this.overlay = false
          this.$store.commit("setMessage", {
            type: "error",
            text: "Failed to deactivate family head."
          });
        }
      }).catch(() => {
        this.overlay = false
        this.$store.commit("setMessage", {
          type: "error",
          text: "Failed to deactivate family head."
        });
      })
    },
    activate() {
      this.overlay = true
      this.activateDialog = false
      fetch("/site/student/activateFamilyHead", {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify({
          practitioner: this.queries.practitioner,
          familyHead: this.currentHead.id
        })
      }).then((response) => {
        if(response.status === 200) {
          this.getFamilyHead()
          this.$store.commit("setMessage", {
            type: "success",
            text: "Family head activated"
          });
        } else {
          this.overlay = false
          this.$store.commit("setMessage", {
            type: "error",
            text: "Failed to activate family head."
          });
        }
      }).catch(() => {
        this.overlay = false
        this.$store.commit("setMessage", {
          type: "error",
          text: "Failed to activate family head."
        });
      })
    },
    getFamilyHead() {
      this.overlay = true
      fetch("/fhir/Basic?_profile=http://ihris.org/fhir/StructureDefinition/itsf-family-head-profile&practitioner=Practitioner/" + this.queries.practitioner).then((response) => {
        response.json().then((entry) => {
          this.overlay = false
          if(entry && entry.entry && entry.entry.length > 0) {
            let head = entry.entry[0].resource
            this.currentHead.id = head.extension.find((ext) => {
              return ext.url === 'http://ihris.org/fhir/StructureDefinition/family-head'
            }).valueReference.reference
            this.currentHead.name = head.extension.find((ext) => {
              return ext.url === 'http://ihris.org/fhir/StructureDefinition/full-name'
            }).valueString
            this.currentHead.regNum = head.extension.find((ext) => {
              return ext.url === 'http://ihris.org/fhir/StructureDefinition/registration-number'
            })?.valueString
            this.currentHead.status = head.extension.find((ext) => {
              return ext.url === 'http://ihris.org/fhir/StructureDefinition/active'
            }).valueCoding.display
            this.currentHead.statusCode = head.extension.find((ext) => {
              return ext.url === 'http://ihris.org/fhir/StructureDefinition/active'
            }).valueCoding.code
          }
        })
      })
    }
  },
  created() {
    this.getFamilyHead()
    fetch("/fhir/Practitioner/" + this.queries.practitioner).then((response) => {
      response.json().then((pract) => {
        this.practitionername = ""
        this.practitionerregnum = pract.extension.find((ext) => {
          return ext.url === 'http://ihris.org/fhir/StructureDefinition/registration-number'
        })?.valueString
        let name = pract.name.find((nm) => {
          nm.use === "official"
        })
        if(!name && pract.name && pract.name.length > 0) {
          name = pract.name[0]
        }
        if(name) {
          if(name.given) {
            if(this.practitionername) {
              this.practitionername += " "
            }
            this.practitionername += name.given.join(" ")
          }
          if(name.family) {
            if(this.practitionername) {
              this.practitionername += " "
            }
            this.practitionername += name.family
          }
        }
      })
    })
  }
}
</script>